import { Component, Event, EventEmitter, Host, Prop, Watch, h } from '@stencil/core';

import { IconName } from '../../atoms/icon/icon.types';

@Component({
  tag: 'bs-accordion-item',
  styleUrl: 'accordion-item.scss',
  shadow: true,
})
export class BsAccordionItem {
  /**
   * Accordion's unique identifier used for accessibility.
   */
  @Prop() identifier!: string;

  // eslint-disable-next-line class-methods-use-this
  @Watch('identifier')
  validateIdentifier(newValue: string) {
    // don't allow `thingToDo` to be the empty string
    const isBlank = typeof newValue !== 'string' || newValue === '';

    if (isBlank) {
      throw new Error('BsAccordionItem identifier is a required property and cannot be empty');
    }
  }

  /**
   * Accordion's title.
   */
  @Prop() accordionItemTitle: string;

  /**
   * Accordion's active state
   */
  @Prop() active?: boolean;

  /**
   * Is the accordion expandable?
   */
  @Prop({ reflect: true }) expandable?: boolean = true;

  /**
   * Accordion's event emitter
   */
  @Event() bsAccordionStateAction: EventEmitter<Boolean>;

  private bsAccordionStateActionHandler(state: Boolean) {
    this.bsAccordionStateAction.emit(state);
  }

  render() {
    const itemIdentifier = this.identifier.replace(/\s+/g, '-').toLowerCase();

    return (
      <Host>
        <li class={`accordion-item ${this.expandable && this.active ? 'is-active' : ''}`}>
          {/* Button */}
          <button
            class="accordion-trigger"
            type="button"
            aria-controls={itemIdentifier}
            aria-expanded={this.expandable && this.active && this.active.toString()}
            id={`accordion-${itemIdentifier}`}
            onClick={() => {
              this.bsAccordionStateActionHandler(this.active);
            }}
            tabIndex={this.expandable ? 1 : -1}
            disabled={!this.expandable}
          >
            <span class="accordion-trigger-title">{this.accordionItemTitle}</span>

            {this.expandable && (
              <bs-icon
                aria-hidden="true"
                class="accordion-trigger-icon"
                name={IconName.CaretDownward}
              />
            )}
          </button>
          {/* Content */}
          <div
            class="accordion-panel"
            role="region"
            aria-hidden={(!this.active).toString()}
            id={itemIdentifier}
          >
            <div class="accordion-panel-content">
              <slot name="accordion-content" />
            </div>
          </div>
        </li>
      </Host>
    );
  }
}
